import React, { useContext, useEffect, useState } from "react";
import { useAppContext } from "../AppContext";

export default function Welcome() {
  const [Opacity, setOpacity] = useState("opacity-0");
  const [OpacitySide, setOpacitySide] = useState("opacity-0");
  const { Reload, setReload, SideRendered, setSideRendered } = useAppContext();

  //## ComponendMount for Initial Intro Rendering of Left Main Panel
  useEffect(() => {
    if (Reload) {
      setTimeout(() => {
        setOpacity("opacity-30");
        setReload(false);
      }, 1500);
    } else {
      setOpacity("opacity-30");
    }
  });

  useEffect(() => {
    if (!SideRendered) {
      setTimeout(() => {
        setOpacitySide("opacity-30");
        setSideRendered(true);
      }, 1700);
    }
  });

  return (
    <div className="flex w-11/12 h-3/4 mx-auto mt-1">
      <div
        className={`rounded-lg bg-white ${Opacity}
      transition-all duration-1000
       w-3/4 h-full`}
      >
      <img className="" src="/home/don/Desktop/JavaScript/Weather App - React/src/Styles/Images/humidity.png" />
      </div>
      <div
        className={`rounded-lg bg-white 
        transition-all duration-1000
      ${SideRendered ? "opacity-30" : `  ${OpacitySide}`}
      w-1/4 `}
      >
        Side
      </div>
    </div>
  );
}
